.popup__title {
  font-family: "Inter", Arial, sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 900;
  line-height: 29px;
  margin: 0 0 48px;
  padding: 0;
}

@media screen and (max-width: 570px) {
  .popup__title {
    font-size: 18px;
    line-height: 21.78px;
    margin: 0 0 75px;
  }
}
